/*Example of Collapsible - Accordion - Expandable View in React Native*/
import React, { Component } from 'react';
//import react in our project
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
} from 'react-native';
import AllCartNavigator from './navigation/AllCartNavigator';
import { createStore, combineReducers } from 'redux';
import {Provider} from 'react-redux';
import unitListReducer from './store/reducers/carts'

global.unitList = [];
const rootReducer = combineReducers({
  unitList: unitListReducer
});
const store = createStore(rootReducer)


export default function App() {
  return (
    <Provider store={store}><AllCartNavigator /></Provider>
  );
}

